import React, { useContext, useState } from 'react';
import { TaskContext } from '../context/TaskContext';
import './AddForm.css';
import firebase from 'firebase/app';
import 'firebase/firestore';
import { AgentContext } from '../context/AgentContext';
import { ActivityContext } from '../context/ActivityContext';

const AddForm = () => {
  const [activity, setActivity] = useState('');
  const [notes, setNotes] = useState('');
  const [owner, setowner] = useState('');

  const { addTask } = useContext(TaskContext);
  const { agents } = useContext(AgentContext);
  const { activities } = useContext(ActivityContext);

  const handle_submit = (e) => {
    e.preventDefault();
    const newTask = {
      date: firebase.firestore.FieldValue.serverTimestamp(),
      activity,
      notes,
      owner,
    };

    addTask(newTask);

    setActivity('');
    setNotes('');
    setowner('');
  };
  return (
    <section className='container'>
      <form className='container add-form py-sm' onSubmit={handle_submit}>
        <select
          name='activity'
          required
          onChange={(e) => setActivity(e.target.value)}
          value={activity}>
          <option value='' disabled defaultValue>
            Activity
          </option>
          {activities.map((activity) => {
            return (
              <option value={activity.title} key={activity.id}>
                {activity.title}
              </option>
            );
          })}
        </select>
        <input
          type='text'
          name='notes'
          placeholder='Add notes'
          onChange={(e) => setNotes(e.target.value)}
          value={notes}
          required
        />
        <select
          name='owner'
          required
          onChange={(e) => setowner(e.target.value)}
          value={owner}>
          <option value='' disabled defaultValue>
            Owner
          </option>
          {agents.map((agent) => {
            return (
              <option value={agent.name} key={agent.id}>
                {agent.name}
              </option>
            );
          })}
        </select>
        <input type='submit' value='Add this task' className='btn btn-accent' />
      </form>
    </section>
  );
};

export default AddForm;
