import React from 'react';

const SearchItem = ({ date, notes, activity, owner }) => {
  const dateText = new Date(date.seconds * 1000);

  return (
    <tr>
      <td>{date && dateText.toDateString()}</td>
      <td>{activity}</td>
      <td>{notes}</td>
      <td>{owner}</td>
    </tr>
  );
};

export default SearchItem;
