import React, { createContext, useState } from 'react';

export const ActivityContext = createContext();

const ActivityContextProvider = ({ children }) => {
    const [activities, setActivities] = useState([
        {id: 1, title: 'Ad-hoc Task'},
        {id: 2, title: 'Conveyancing'},
        {id: 3, title: 'Filing - Expenses'},
        {id: 4, title: 'Filing - Income'},
        {id: 5, title: 'Leads Management'},
        {id: 6, title: 'Leads Management - ISA'},
        {id: 7, title: 'Listing Emails'},
        {id: 8, title: 'Listing Task'},
        {id: 9, title: 'New Deal - Agent Double-Ender'},
        {id: 10, title: 'New Deal - Buyer Side'},
        {id: 12, title: 'New Deal - Referral'},
        {id: 13, title: 'New Deal - Seller Side'},
        {id: 14, title: 'New Listing'},
        {id: 15, title: 'Reports'},
        {id: 16, title: 'Review Request Email'},
        {id: 17, title: 'Social Media Task'},
    ]);

    return (
        <ActivityContext.Provider value={{ activities }}>
            {children}
        </ActivityContext.Provider>
    )
}

export default ActivityContextProvider
