import React, { createContext, useState } from 'react';

export const AgentContext = createContext();

const AgentContextProvider = ({ children }) => {    
    const [agents, setAgents] = useState([
        {id: 1, name: 'Admin Team'},
        {id: 2, name: 'Chris Duffy'},
        {id: 3, name: 'Christopher Audette'},
        {id: 4, name: 'Frank Liu'},
        {id: 5, name: 'Jen Johnson'},
        {id: 6, name: 'Maria Shettler'},
        {id: 7, name: 'May D, Curtis K.'},
        {id: 8, name: 'Mike Litorco'},
        {id: 9, name: 'Sarah Walczak'},
        {id: 10, name: 'Will Vo'},
        {id: 11, name: 'Referral Agent'},    
    ]);

    return (
        <AgentContext.Provider value={{ agents }}>
            {children}
        </AgentContext.Provider>
    )
}

export default AgentContextProvider
