import React, { createContext, useEffect, useState } from 'react';
import { projectFirestore } from '../config/firebaseConfig';

export const TaskContext = createContext();

const TaskContextProvider = ({ children }) => {
  const [tasks, setTasks] = useState([]);
  const [searches, setSearches] = useState([]);

  function addTask(newTask) {
    projectFirestore.collection('tasks').add(newTask);
  }

  function getTasks() {
    projectFirestore
      .collection('tasks')
      .orderBy('date', 'desc')
      .limit(9)
      .onSnapshot((task) => {
        const tasksList = [];
        task.docs.forEach((doc) =>
          tasksList.push({ ...doc.data(), id: doc.id })
        );
        setTasks(tasksList);
      });
  }

  function getSearch(terms) {
    const searchDate = new Date(terms.date);
    projectFirestore
      .collection('tasks')
      .where('activity', '==', terms.activity)
      .where('owner', '==', terms.owner)
      .where('date', '>=', searchDate)
      .get()
      .then((task) => {
        const tasksList = [];
        task.docs.forEach((doc) =>
          tasksList.push({ ...doc.data(), id: doc.id })
        );
        setSearches(tasksList);
      });
  }

  function deleteTask(id) {
    projectFirestore.collection('tasks').doc(id).delete();
  }

  useEffect(() => {
    getTasks();
  }, []);

  console.log(tasks);
  useEffect(() => getTasks(), []);
  console.log();
  return (
    <TaskContext.Provider
      value={{ tasks, getSearch, searches, addTask, deleteTask }}>
      {children}
    </TaskContext.Provider>
  );
};

export default TaskContextProvider;
