import React, { useContext } from 'react';
import { TaskContext } from '../context/TaskContext';
import { motion } from 'framer-motion';

const TaskItem = ({ task }) => {
  const { deleteTask } = useContext(TaskContext);
  const { owner, notes, activity, date, id } = task;

  return (
    <motion.li layout className='task-item'>
      <div className='task-item__head'>
        <p>
          <i className='fas fa-user'></i> <span>{owner}</span>
        </p>
        <p>
          <i className='fas fa-running'></i> <span>{activity}</span>
        </p>
      </div>
      <div className='task-item__body'>
        <p>{notes}</p>
      </div>
      <div className='task-item__foot'>
        <p>
          <i className='fas fa-calendar-check' style={{ color: 'black' }}></i>{' '}
          Created on: {date && date.toDate().toDateString()}
        </p>
        <p class='task-delete' onClick={() => deleteTask(id)}>
          <i className='fas fa-trash'></i> Delete
        </p>
      </div>
    </motion.li>
  );
};

export default TaskItem;
