import React, { useContext, useState } from 'react';
import './AddForm.css';
import { AgentContext } from '../context/AgentContext';
import { ActivityContext } from '../context/ActivityContext';
import { TaskContext } from '../context/TaskContext';

const SearchForm = () => {
  const [activity, setActivity] = useState('');
  const [date, setDate] = useState('');
  const [owner, setowner] = useState('');

  const { getSearch } = useContext(TaskContext);
  const { agents } = useContext(AgentContext);
  const { activities } = useContext(ActivityContext);

  const handle_submit = (e) => {
    e.preventDefault();
    const search_terms = {
      date,
      activity,
      owner,
    };

    getSearch(search_terms);
  };
  return (
    <section className='container'>
      <form
        className='container add-form py-sm search-form'
        onSubmit={handle_submit}>
        <select
          name='activity'
          required
          onChange={(e) => setActivity(e.target.value)}
          value={activity}>
          <option value='' disabled defaultValue>
            Activity
          </option>
          {activities.map((activity) => {
            return (
              <option value={activity.title} key={activity.id}>
                {activity.title}
              </option>
            );
          })}
        </select>
        <input
          type='date'
          name='notes'
          placeholder='Add notes'
          onChange={(e) => setDate(e.target.value)}
          value={date}
          required
        />
        <select
          name='owner'
          required
          onChange={(e) => setowner(e.target.value)}
          value={owner}>
          <option value='' disabled defaultValue>
            Owner
          </option>
          {agents.map((agent) => {
            return (
              <option value={agent.name} key={agent.id}>
                {agent.name}
              </option>
            );
          })}
        </select>
        <input type='submit' value='Search Tasks' className='btn btn-accent' />
      </form>
    </section>
  );
};

export default SearchForm;
